<div class="w-[100vw] md:w-[50vw] h-[80vh] md:h-[90vh] flex flex-col items-center justify-start"><div class="flex p-1 justify-center items-center"><input class="w-[50%] p-2 bg-white focus:outline-none focus:shadow-outline rounded-md py-2 px-2 block appearance-none leading-normal" v-model="guessNum" :class="[         errorMessage ? 'border border-red-500' : 'border border-gray-500',       ]" type="" placeholder="輸入要猜的數字"><button class="bg-blue-600 text-white ml-3 py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-blue-500" @click="action">送出</button><button class="bg-red-600 text-white ml-3 py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-red-500" @click="tipStatus = !tipStatus">提示</button></div><div class="text-base text-rose-600" v-if="errorMessage">{{errorMessage}}</div><div class="w-full flex mt-1 p-2 justify-around items-center"><div>數字</div><div>AB</div><div v-if="tipStatus">提示</div></div><div class="w-full h-auto max-h-[45vh] md:max-h-[55vh] mt-2 overflow-auto" ref="mainView"><div class="w-full flex p-2 justify-around items-center" v-for="item in data"><div>{{item.guessNum}}</div><div>{{item.AB}}</div><div v-if="tipStatus">{{item.tip}}</div></div></div><div class="w-full h-auto overflow-auto flex flex-col" v-if="endStatus"><div class="my-1 text-xl text-red-700">猜中了 共猜了{{times}}次</div><div><button class="bg-green-500 text-white py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-green-400" @click="madeNum">再玩一次</button></div></div></div>