<div class="w-full h-[100vh] flex flex-wrap items-center justify-center"><div class="drop-zone w-full md:w-[600px] h-[200px] md:h-[400px] flex flex-col items-center justify-center cursor-pointer border-[#009578] border-4 rounded-2xl bg-[#e0ffb5]" ref="fileDiv" :class="[       borderStyle ? 'border-solid' : 'border-dashed',     ]" @click="choseFile" @drop="dropFile" @dragover="dragOver" @dragleave="setBorder" @dragend="setBorder"><span class="drop-zone__prompt">Drop file here or click to upload</span><input class="drop-zone__input hidden" ref="fileInput" type="file" name="myFile" @change="changeFile"></div><div class="w-full md:w-[80%] h-auto px-1"><el-table v-if="tableData.length" :data="tableData" :height="isMobile ? 300 : 500" style="width: 100%"><el-table-column prop="date" label="Date" width="180"></el-table-column><el-table-column prop="name" label="Name" width="180"></el-table-column><el-table-column prop="address" label="Address"></el-table-column></el-table></div></div>