<div class="w-[350px] md:w-[500px] h-[80vh] md:h-[90vh] mt-[-10px] flex flex-col items-center justify-start"><div class="flex flex-wrap justify-center items-center"><div class="w-[100px] h-[100px] md:w-[150px] md:h-[150px] m-1 flex items-center justify-center text-5xl bg-green-400" v-if="viewStatus &amp;&amp; !endText" v-for="(item,index) in data" :key="index" @click="action(item)" v-text="displayStatus ? item : ''"></div><div class="flex justify-end items-end text-4xl mt-10" v-else>{{endText}}</div></div><div class="flex justify-center items-center mt-10"><div class="text-4xl" v-if="!viewStatus">猜{{trueNum}}</div><button class="bg-green-600 text-white ml-3 py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-green-500" v-if="!data.length" v-text="hasChange ? '再玩一次' : '開始'" @click="play"></button></div></div>