<div class="w-[310px] md:w-[400px] h-auto flex flex-col items-center justify-start"><div class="flex flex-wrap justify-center items-center"><div v-for="(item, index) in land[0].length"><div class="w-[30px] h-[30px] md:w-[40px] md:h-[40px] border-2 border-[#b59ec2] flex justify-center items-center " v-for="(items, indexs) in land" :class="[land[indexs][index].check ? 'bg-[#fcfccf]' : 'bg-[#79fcac]']" @click="action(indexs,index)">{{land[indexs][index].display}}</div></div></div><div class="flex flex-col justify-center items-center m-1"><div class="my-1" v-text="'現在模式: ' + (flagStatus ? '標記' : '點擊')"></div><button class="text-white py-2 px-4 font-medium rounded-xl transition-all duration-300" :class="[flagStatus ? 'bg-green-500 hover:bg-green-400' : 'bg-red-500 hover:bg-red-400']" @click="flagStatus = !flagStatus">切換 點擊/標記</button></div><div class="flex flex-col justify-center items-center" v-if="endStatus"><div class="flex flex-col justify-center items-center text-2xl my-1" v-text="isWin ? '贏了' : '暴了'" :class="[isWin ? 'text-green-500' : 'text-red-500']"></div><button class="bg-green-500 text-white py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-green-400" @click="init">再玩一次</button></div></div>